import React, {lazy, Suspense} from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import Loading from "../../../../components/shared-components/Loading";
import {Redirect, Route, Switch} from "react-router-dom";
import LayoutContent from "../../../../components/layout-components/LayoutContent";

const AccountViews = ({match})=> {
    return (
        <Suspense fallback={<Loading cover="content"/>}>
            <Switch>
                <Route path={`${ match.url }/profile`} component={lazy(() => import(`./profile/index`))} />
                <Route path={`${ match.url }/change-password`} component={lazy(() => import(`./change-password/index`))} />
                {/*<Redirect to={`${match.url}/profile`} from={match.url}/>*/}
            </Switch>
        </Suspense>
    )
}

AccountViews.propTypes = {}
AccountViews.defaultProps = {}

export default connect(()=> {
    return {}
},{})(AccountViews)
